@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.list-links-container-none {
  display: flex; }

.list-links-container {
  display: flex; }

.list-links li {
  display: inline;
  padding: 5px; }

.list-links li a {
  letter-spacing: 1.5px;
  color: rgba(21, 169, 255, 0.938);
  padding: 5px; }

.list-links li a:hover {
  color: #75bcff;
  padding-bottom: 10px;
  border-bottom: 2px solid #99ccff; }

.list-burger-container {
  display: flex;
  align-self: center;
  padding: 5px; }

.list-burger {
  display: none;
  background: transparent;
  color: rgba(215, 233, 255, 0.87);
  padding: 5px 10px;
  font-weight: bold;
  border: 2px solid rgba(215, 233, 255, 0.87); }

.list-burger li {
  font-size: 2em;
  color: rgba(21, 169, 255, 0.938); }

@media (max-width: 768px) {
  .list-links-container-none * {
    display: none; }
  .list-links-container-abs {
    position: fixed;
    width: 100%;
    height: 100%;
    background: #030331;
    display: flex;
    justify-content: center; }
  .list-links {
    display: flex;
    justify-content: center;
    flex-direction: column; }
  .list-links li {
    padding: 15px;
    font-size: 2em; }
  .list-burger {
    display: block;
    width: 100%;
    height: 100%;
    padding: 10px; }
  .list-burger li {
    align-self: center; }
  .list-burger-container {
    position: fixed;
    display: inline;
    z-index: 10;
    width: 10%;
    min-width: 55px; } }

.nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 60px;
  background: #060622; }

.nav-title {
  color: rgba(234, 237, 241, 0.932);
  font-size: 115%; }

.nav-spacer {
  padding: 0px 10px 0px 0px;
  font-size: 2em;
  color: rgba(255, 255, 255, 0.2); }

.nav-icon {
  color: #f00000;
  letter-spacing: -5px;
  font-family: "Courier New", Courier, monospace;
  font-size: 150%; }

.section-home, .section-content {
  text-align: center; }

.section-home p {
  width: 100%; }

.section-home h2 {
  font-size: 130%; }

.section-content h3 {
  font-size: 120%; }

.img-columns {
  height: 100%; }

.img-column {
  height: 100%; }

.img-right {
  float: right; }

@media (max-width: 768px) {
  .img-right {
    float: none; }
  .img-parent * {
    display: flex;
    align-self: center; }
  .img-parent .column {
    align-self: center; }
  .img-link {
    align-self: right;
    width: auto;
    float: right; } }

.contact-fields {
  text-align: center; }

.img-column {
  height: auto; }

* {
  font-family: "Roboto", sans-serif; }

h2, h3 {
  text-align: center;
  font-weight: bold; }

#root {
  height: 100%; }

.flex {
  display: flex; }

.flex-column {
  flex-direction: column; }

.flex-item-container {
  align-items: center; }

.flex-spaced {
  justify-content: space-around; }

.flex-item-center {
  align-self: center; }

.flex-item-left {
  align-self: flex-start; }

.flex-item-right {
  align-self: flex-end; }

.flex-item {
  flex-grow: 1; }

.flex-item-wide {
  flex-grow: 2; }

.flex-hoz-center {
  justify-content: center; }

.flex-hoz-left {
  justify-content: left; }

.flex-hoz-right {
  justify-content: right; }

.flex-vert-center {
  align-content: center; }

.flex-rtl {
  flex-direction: row-reverse; }

.full {
  width: 100%; }

html {
  background: #eff0f8;
  height: 100%; }

body {
  height: 100%; }

.main {
  width: 100%;
  height: 100%;
  background: #eff0f8; }

.body {
  margin-top: 60px;
  height: 100%;
  width: 100%;
  background: #eff0f8; }

.body-item {
  width: 80%;
  height: 100%;
  border-radius: 0px 0px 3px 3px;
  box-shadow: 0px 2px 6px 4px rgba(240, 240, 240, 0.877);
  padding: 15px;
  background: white; }

.padding-side {
  padding-left: 10px;
  padding-right: 10px; }

.left {
  float: left; }

.right {
  float: right; }

.db-border {
  border: 2px solid yellow; }

.db-border * {
  border: 2px dotted red; }

.text-light {
  color: white; }

@media (max-width: 768px) {
  .body-item {
    width: 100%; } }

