
.img-columns {
	height: 100%;	
}

.img-column {
	height: 100%;
}

.img-right {
	float: right;
}

@media(max-width: 768px) {
	.img-right {
		float: none;
	}

	.img-parent * {
		display: flex;
		align-self: center;
		
	}
	.img-parent .column {
		align-self: center;
		
	}

	.img-link {
		align-self: right;
		width: auto;
		float: right;
	}
}

